import './public-path';
import Vue from 'vue';
import App from './App.vue';

Vue.config.productionTip = false;

let instance = null;
function render(props = {}) {
  const { container } = props;
  Vue.prototype.setGlobalState = props.setGlobalState;
  instance = new Vue({
    render: (h) => h(App),
  }).$mount(container ? container.querySelector('#title-container') : '#title-container');
}

// 独立运行时
if (!window.__POWERED_BY_QIANKUN__) {
  render();
}

export async function bootstrap() {
  console.log('[title app] bootstraped');
}
export async function mount(props) {
  console.log('[title app] props from main framework', props);
  render(props);
}
export async function unmount() {
  instance.$destroy();
  instance.$el.innerHTML = '';
  instance = null;
}